'use strict';

var $ = require('jquery');

// Constructor
var Faq = function() {

  var fullId;
  var id;
  var answerId;

  // FAQ Dropdowns
  $('.faq-panel .question').click(function(e) {
    fullId = e.target.id;
    id = fullId.substr(fullId.length - 1);
    answerId = '#answer-' + id;
    $(answerId).slideToggle();
  })

};

module.exports = Faq;
